.todo-list {
  background-color: var(--bg-body);
  box-shadow: 0px -33px 50px -2px rgba(194, 195, 214, 0.5);
  border-radius: 5px;

  .list-item {
    display: flex;
    gap: 12px;
    align-items: center;
    padding-inline: 20px;
    padding-block: 16px;
    border-bottom: 1px solid black;
  }

  @media (min-width: 48rem) {
    .list-item {
      padding-block: 23px;
    }
  }

  .complete-btn {
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 1px solid var(--clr-lightest-gray-100);
    height: 20px;
    width: 20px;
    border-radius: 50%;
  }

  .complete-btn:hover {
    border-image: var(--clr-primary-gradient);
  }

  @media (min-width: 48rem) {
    .complete-btn {
      height: 24px;
      width: 24px;
    }
  }

  .delete-btn {
    margin-left: auto;
  }
}
